.App {
	text-align: center;
}

.App-logo {
	height: 4vh;
}

.App-header {
	background-color: #282c34;
	height: 0vh;
	display: flex;
	flex-direction: column;
	font-size: calc(10px + 2vmin);
}

.App-body {
	background: #355c7d; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#c06c84,
		#6c5b7b,
		#355c7d
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#c06c84,
		#6c5b7b,
		#355c7d
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	height: 100vh;
	font-size: calc(10px + 2vmin);
	color: seashell;
}

.App-body p {
	animation: 2s ease 0s 1 fadeIn;
}

.App-input {
	width: 15em;
	height: 1.6em;
	color: gray;
	border: none;
	border-radius: 5px;
	outline: none;
	padding: 0.5rem;
	box-shadow: 0 0 7px 0 lightyellow, 0 10px 25px 0 rgba(0, 0, 0, 0.2);
	font-size: medium;
	margin-top: 5vh;
	text-align: center;
	animation: 2s ease 0s 1 fadeIn;
}

.App-input::placeholder {
	color: gray;
}

.App-input:hover {
	box-shadow: 0 0 10px 0 lightpink, 0 10px 25px 0 rgba(0, 0, 0, 0.2);
}

.App-info img {
	margin: -15px;
	filter: drop-shadow(0px 0px 1px seashell);
	animation: 2s ease 0s 1 fadeIn;
}

.App-info p {
	font-size: calc(40px + 2vmin);
	margin: auto;
	color: lightpink;
}

.App-info p.Temp-description {
	font-size: calc(13px + 1vmin);
	margin-bottom: 5px;
	animation: 2s ease 0s 1 fadeIn;
	text-transform: capitalize;
	color: seashell;
}

.App-footer a {
	font-size: calc(6px + 2vmin);
	color: rgba(255, 255, 255, 1);
	animation: 2s ease 0s 1 fadeIn;
}

@media (min-width: 1025px) {
	.App-footer a {
		position: absolute;
		bottom: 3vh;
		left: 40vw;
	}

	.App-body {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
}

@media (max-width: 1024px) {
	.App-body {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		height: 87vh;
	}
}

.App-footer a:hover {
	color: rgb(96, 198, 221);
}

@keyframes blinker {
	50% {
		opacity: 0;
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 100;
	}
}
